<template>

    <div class="loans-issued-page">

        <div class="go-back-link">
            <div class="go-back-link">
                <router-link to="/"><span class="icon-arrow-left"></span>На главную</router-link>
            </div>
        </div>

        <div class="page-title">
            <h1>Выданные кредиты</h1>
        </div>

        <div class="block">
            <div class="block-content">

                <div v-if="ordersFinished ? ordersFinished.length > 0 : false">
                    <div class="block-title">
                        <div class="h3">Выданные кредиты</div>
                    </div>
                    <div class="block-content">
                        <LoansIssuedTable :orders="ordersFinished"/>
                    </div>
                </div>

                <div
                    v-else
                    class="loans-false">Отсутствуют выданные кредиты</div>

            </div>
        </div>

    </div>

</template>

<script>

    import { mapState } from 'vuex'
    import LoansIssuedTable from "@/components/Blocks/LoansIssued"

    export default {
        name: "LoansIssued",
        components: {
            LoansIssuedTable
        },
        computed: {
            ...mapState({
                ordersFinished: state => state.orders.ordersFinished
            })
        },
        async mounted() {
            await this.$store.dispatch('getOrdersFinished')
        }
    }
</script>

